.header {
  background: gold;

  h1 {
    text-align: center;
    font-size: 50px;
  }
}

section {
  height: 50vh;
  background-image: url('../assets/home.jpg');
  background-repeat: no-repeat;
  background-position: center;
  .content {
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 2em;
    text-align: center;
    vertical-align: middle;
  }
}

.buttons {
  display: flex;
  justify-content: space-around;
  .buttonVerre{
    background-color: black;
    color: white;
    border-radius: 30px;
    padding: 10px;
    margin-top: 3em;
  }

  .buttonBurger{
    background-color: black;
    color: white;
    border-radius: 30px;
    padding: 10px;
    margin-top: 3em;
  }
}